import React from 'react';
import './about.css';
import aboutImage from '../assets/cafe.jpg'; // Asegúrate de tener esta imagen en tu carpeta de assets

const About = () => {
  return (
    <div className="about-section">
      <div className="about-image" style={{ backgroundImage: `url(${aboutImage})` }}></div>
      <div className="about-text">
        <h2>Nosotros</h2>
        <p>
          Our specialty coffees, under the CROSSMI brand and for private label, are the result of meticulous processes and a passion for excellence. We offer an exceptional selection of varieties, backed by prestigious certifications such as the 100% Colombian Coffee Program, Denomination of Origin, and Protected Geographical Indication.
        </p>
      </div>
    </div>
  );
};

export default About;
