import React from 'react';
import product1 from '../assets/product1.png';
import product2 from '../assets/product2.png';
import product3 from '../assets/product3.png';

function Products() {
  return (
    <section id="products" className="products">
      <h2>Productos</h2>
      <div className="product-list">
        <div className="product-item">
          <img src={product1} alt="Pink Bourbon" />
          <h3>Pink Bourbon</h3>
          <p>Origin: Planadas, Gaitania<br/>Altitude: 1500 - 1800 M.A.S.L.<br/>Flavors: Red fruits, blackberries, cherry, caramel.</p>
        </div>
        <div className="product-item">
          <img src={product2} alt="Geisha" />
          <h3>Geisha</h3>
          <p>Origin: Planadas, Gaitania<br/>Altitude: 1500 - 1800 M.A.S.L.<br/>Flavors: Cherries, Honey, Dehydrated Grapes, Panela, Red Fruits.</p>
        </div>
        <div className="product-item">
          <img src={product3} alt="Blend" />
          <h3>Blend</h3>
          <p>Origin: Planadas, Gaitania<br/>Altitude: 1500 - 1800 M.A.S.L.<br/>Flavors: Caramel, Chocolate, Red fruits, Cinnamon.</p>
        </div>
      </div>
    </section>
  );
}

export default Products;
